/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * Internal dependencies
 */
import {
	computeClassName as generalSettingsClasses,
	GeneralSettingsPanel,
} from './general';
import {
	computeClassName as responsiveClasses,
	ResponsiveTabPanel,
} from './responsive';

export function computeClassName( attributes ) {
	return clsx(
		generalSettingsClasses( attributes ),
		responsiveClasses( attributes )
	);
}

export const BootstrapSettingsPanels = ( {
	setAttributes,
	attributes,
	supportedSettings,
} ) => {
	return (
		<>
			{supportedSettings.general && (
			<GeneralSettingsPanel
				setAttributes={ setAttributes }
				attributes={ attributes }
				supportedSettings={ supportedSettings.general }
			/>
			)}
			{supportedSettings.responsive && (
			<ResponsiveTabPanel
				setAttributes={ setAttributes }
				attributes={ attributes }
				supportedSettings={ supportedSettings.responsive }
			/>
			)}
		</>
	);
};
