/**
 * WordPress dependencies
 */
import { useMemo } from '@wordpress/element';
import { Panel, PanelBody, RangeControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

/**
 */
import { breakpoints } from '.';
const breakpointValues = {
	sm: 576,
	md: 768,
	lg: 992,
	xl: 1200,
	xxl: 1400,
};
export function getDataValues( attributes ) {
	const carouselSettings = {
		...attributes.carousel,
		mobileFirst: true,
		responsive: [],
	};
	breakpoints.forEach( ( breakpoint ) => {
		if (
			attributes[ breakpoint ] &&
			Object.keys( attributes[ breakpoint ] ).length
		) {
			if (
				attributes[ breakpoint ].carousel !== undefined &&
				Object.keys( attributes[ breakpoint ].carousel ).length
			) {
				carouselSettings.responsive.push( {
					breakpoint: breakpointValues[ breakpoint ],
					settings: attributes[ breakpoint ].carousel,
				} );
			}
		}
	} );
	return {
		'data-slick': JSON.stringify( carouselSettings ),
	};
}

export function CarouselPanel( {
	breakpoint,
	attributes,
	setBreakpointAttribute,
} ) {
	const setCarouselAttribute = ( setting, value ) => {
		if ( breakpoint === 'xs' ) {
			setBreakpointAttribute( 'carousel', setting, value );
		} else {
			setBreakpointAttribute( breakpoint, 'carousel', {
				...attributes[ breakpoint ].carousel,
				[ `${ setting }` ]: value,
			} );
		}
	};

	const settings = useMemo( () => {
		if ( breakpoint === 'xs' ) {
			return {
				dots: attributes.carousel?.dots,
				arrows: attributes.carousel?.arrows,
				slidesToShow: parseInt( attributes.carousel?.slidesToShow ),
				slidesToScroll: parseInt( attributes.carousel?.slidesToScroll ),
				centerMode: attributes.carousel?.centerMode,
			};
		}
		return {
			dots: attributes[ breakpoint ].carousel?.dots,
			arrows: attributes[ breakpoint ].carousel?.arrows,
			slidesToShow: parseInt(
				attributes[ breakpoint ].carousel?.slidesToShow
			),
			slidesToScroll: parseInt(
				attributes[ breakpoint ].carousel?.slidesToScroll
			),
			centerMode: attributes[ breakpoint ].carousel?.centerMode,
		};
	}, [ attributes, breakpoint ] );

	return (
		<Panel>
			<PanelBody title="Carousel Settings" initialOpen={ true }>
				<RangeControl
					__nextHasNoMarginBottom="true"
					__next40pxDefaultSize="true"
					label={
						__( 'Slides to show' ) +
						' (' +
						settings.slidesToShow +
						')'
					}
					value={ settings.slidesToShow }
					onChange={ ( newVal ) =>
						setCarouselAttribute(
							'slidesToShow',
							isNaN( newVal ) ? undefined : parseInt( newVal )
						)
					}
					min={ 1 }
					max={ 10 }
					allowReset={ true }
				/>
				<RangeControl
					__nextHasNoMarginBottom="true"
					__next40pxDefaultSize="true"
					label={
						__( 'Slides to scroll' ) +
						' (' +
						settings.slidesToScroll +
						')'
					}
					value={ settings.slidesToScroll }
					onChange={ ( newVal ) =>
						setCarouselAttribute(
							'slidesToScroll',
							isNaN( newVal ) ? undefined : parseInt( newVal )
						)
					}
					min={ 1 }
					max={ 10 }
					allowReset={ true }
				/>
				<RangeControl
					__nextHasNoMarginBottom="true"
					__next40pxDefaultSize="true"
					label={ __( 'Rows' ) + ' (' + settings.rows + ')' }
					value={ settings.rows }
					onChange={ ( newVal ) =>
						setCarouselAttribute(
							'rows',
							isNaN( newVal ) ? undefined : parseInt( newVal )
						)
					}
					min={ 1 }
					max={ 10 }
					allowReset={ true }
				/>
				<RangeControl
					__nextHasNoMarginBottom="true"
					__next40pxDefaultSize="true"
					label={
						__( 'Slides per Row' ) +
						' (' +
						settings.slidesPerRow +
						')'
					}
					value={ settings.slidesPerRow }
					onChange={ ( newVal ) =>
						setCarouselAttribute(
							'slidesPerRow',
							isNaN( newVal ) ? undefined : parseInt( newVal )
						)
					}
					min={ 1 }
					max={ 10 }
					allowReset={ true }
				/>
			</PanelBody>
		</Panel>
	);
}
