/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import { Panel, PanelBody } from '@wordpress/components';

/**
 * Internal dependencies
 */
import {
	BorderPanel,
	getClassName as getBorderClassName,
} from './border_panel.js';
import {
	PlacementsPanel,
	getClassName as getPlacementsClassName,
} from './placements_panel.js';
import {
	SpacingsPanel,
	getClassName as getSpacingsClassname,
} from './spacings_panel.js';
import {
	SizesPanel,
	getClassName as getSizesClassName,
} from './sizes_panel.js';

export function computeClassName( attributes ) {
	return clsx(
		getBorderClassName( attributes ),
		getPlacementsClassName( attributes ),
		getSpacingsClassname( attributes ),
		getSizesClassName( attributes )
	);
}

export function GeneralSettingsPanel( {
	setAttributes,
	attributes,
	supportedSettings,
} ) {
	const { border, sizes, placements, spacings } = supportedSettings;

	const setTypeAttribute = ( type, key, value ) => {
		setAttributes( {
			[ type ]: {
				...attributes[ type ],
				[ key ]: value,
			},
		} );
	};

	return (
		<Panel>
			<PanelBody title="General Settings" initialOpen={ false }>
				{ border && attributes.border && (
					<>
						<BorderPanel
							setAttribute={ setTypeAttribute }
							attributes={ attributes }
							supportedSettings={ border }
						/>
					</>
				) }
				{ placements && attributes.placements && (
					<>
						<PlacementsPanel
							setAttribute={ setTypeAttribute }
							attributes={ attributes }
							supportedSettings={ placements }
						/>
					</>
				) }
				{ spacings && attributes.spacings && (
					<>
						<SpacingsPanel
							setAttribute={ setTypeAttribute }
							attributes={ attributes }
							supportedSettings={ spacings }
						/>
					</>
				) }
				{ sizes && attributes.sizes && (
					<>
						<SizesPanel
							setAttribute={ setTypeAttribute }
							attributes={ attributes }
							supportedSettings={ sizes }
						/>
					</>
				) }
			</PanelBody>
		</Panel>
	);
}
