/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import {
	Path,
	SVG,
	RangeControl,
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export function getClassName( attributes ) {
	return clsx(
		attributes.xs && {
			[ `col-${ attributes.xs.colSpan }` ]:
				attributes.xs.colSpan !== undefined &&
				attributes.xs.colSpan > 1,
			[ `row-cols-${ attributes.xs.rowCols }` ]:
				attributes.xs.rowCols !== undefined &&
				attributes.xs.rowCols > 0,
			[ `ratio ratio-${ attributes.xs.ratio }` ]:
				attributes.xs.ratio !== undefined,
		},
		[ 'sm', 'md', 'lg', 'xl', 'xxl' ].map( ( val ) => {
			return attributes[ val ]
				? {
						[ `col-${ val }-${ attributes[ val ].colSpan }` ]:
							attributes[ val ].colSpan !== undefined &&
							attributes[ val ].colSpan > 1,
						[ `row-cols-${ val }-${ attributes[ val ].rowCols }` ]:
							attributes[ val ].rowCols !== undefined &&
							attributes[ val ].rowCols > 0,
						[ `ratio ratio-${ val }-${ attributes[ val ].ratio }` ]:
							attributes[ val ].ratio !== undefined,
				  }
				: {};
		} )
	);
}

export function SizesPanel( {
	breakpoint,
	attributes,
	setBreakpointAttribute,
	supportedSettings,
} ) {
	const resetAll = () => {
		setBreakpointAttribute( breakpoint, 'colSpan', undefined );
		setBreakpointAttribute( breakpoint, 'rowCols', undefined );
		setBreakpointAttribute( breakpoint, 'ratio', undefined );
	};

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Sizes" resetAll={ resetAll }>
			<small className="text-muted" style={ titleStyle }>
				Configure component sizes
			</small>
			{ supportedSettings.colSpan && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].colSpan !== undefined
					}
					label={ __( 'Total columns spanned' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'colSpan',
							undefined
						)
					}
				>
					<RangeControl
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						label={
							__( 'Column Span' ) +
							' (' +
							attributes[ breakpoint ].colSpan +
							')'
						}
						value={ parseInt( attributes[ breakpoint ].colSpan ) }
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'colSpan',
								isNaN( newVal ) ? undefined : parseInt( newVal )
							)
						}
						min={ 0 }
						max={ 12 }
						withInputField={ false }
						allowReset={ true }
					/>
				</ToolsPanelItem>
			) }
			{ supportedSettings.rowCols && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].rowCols !== undefined
					}
					label={ __( 'Total columns size' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'rowCols',
							undefined
						)
					}
				>
					<RangeControl
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						label={
							__( 'Column Count' ) +
							' (' +
							attributes[ breakpoint ].rowCols +
							')'
						}
						value={ parseInt( attributes[ breakpoint ].rowCols ) }
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'rowCols',
								isNaN( newVal ) ? undefined : parseInt( newVal )
							)
						}
						min={ 0 }
						max={ 12 }
						withInputField={ false }
						allowReset={ true }
					/>
				</ToolsPanelItem>
			) }
			{ supportedSettings.ratio && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].ratio !== undefined
					}
					label={ __( 'Block size ratio' ) }
					onDeselect={ () =>
						setBreakpointAttribute( breakpoint, 'ratio', undefined )
					}
				>
					<ToggleGroupControl
						label="Block ratio"
						value={ attributes[ breakpoint ].ratio }
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'ratio',
								newVal
							)
						}
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
					>
						{ [
							{
								id: '1x1',
								name: '1x1',
								path: 'M0,0H20V20H0Z',
							},
							{
								id: '2x1',
								name: '2x1',
								path: 'M0,0H40V20H0Z',
							},
							{
								id: '1x2',
								name: '1x2',
								path: 'M0,0H20V40H0Z',
							},
							{
								id: '4x3',
								name: '4x3',
								path: 'M0,0H40V30H0Z',
							},
							{
								id: '3x4',
								name: '3x4',
								path: 'M0,0H30V40H0Z',
							},
							{
								id: '16x9',
								name: '16x9',
								path: 'M0,0H32V18H0Z',
							},
							{
								id: '21x9',
								name: '21x9',
								path: 'M0,0H42V18H0Z',
							},
						].map( ( item ) => {
							return (
								<ToggleGroupControlOption
									className="align-items-start"
									key={ item.id }
									value={ item.id }
									label={
										<div className="vstack">
											<SVG
												xmlns="http://www.w3.org/2000/svg"
												width="100%"
												height="auto"
												viewBox="0 0 48 48"
											>
												<Path d={ item.path } />
											</SVG>
											{ item.name }
										</div>
									}
								/>
							);
						} ) }
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
