/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import { Icon, cog } from '@wordpress/icons';
import {
	RangeControl,
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export function getClassName( attributes ) {
	return clsx(
		attributes.xs && {
			[ `d-${ attributes.xs.display }` ]:
				attributes.xs.display !== undefined,
			[ `opacity-${ attributes.xs.opacity }` ]:
				attributes.xs.opacity !== undefined &&
				! isNaN( attributes.xs.opacity ),
		},
		[ 'sm', 'md', 'lg', 'xl', 'xxl' ].map( ( val ) => {
			return attributes[ val ]
				? {
						[ `d-${ val }-${ attributes[ val ].display }` ]:
							attributes[ val ].display !== undefined,
						[ `opacity-${ val }-${ attributes[ val ].opacity }` ]:
							attributes[ val ].opacity !== undefined &&
							! isNaN( attributes[ val ].opacity ),
				  }
				: {};
		} )
	);
}

export function DisplayPanel( {
	breakpoint,
	attributes,
	setBreakpointAttribute,
	supportedSettings,
} ) {
	const resetAll = () => {
		setBreakpointAttribute( breakpoint, 'display', undefined );
		setBreakpointAttribute( breakpoint, 'opacity', undefined );
	};

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Display" resetAll={ resetAll }>
			<small className="text-muted" style={ titleStyle }>
				Configure component visability and display type
			</small>
			{ supportedSettings.display && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].display !== undefined
					}
					label={ __( 'Display' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'display',
							undefined
						)
					}
				>
					<ToggleGroupControl
						label="Display"
						value={ attributes[ breakpoint ].display }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'display',
								newVal
							)
						}
					>
						<ToggleGroupControlOption
							value="flex"
							label="Flex (default)"
						/>
						<ToggleGroupControlOption
							value="inline"
							label="Inline"
						/>
						<ToggleGroupControlOption value="block" label="Block" />
						<ToggleGroupControlOption value="grid" label="Grid" />
						<ToggleGroupControlOption value="none" label="None" />
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
			{ supportedSettings.opacity && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].opacity !== undefined &&
						! isNaN( attributes[ breakpoint ].opacity )
					}
					label={ __( 'Opacity' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'opacity',
							undefined
						)
					}
				>
					<RangeControl
						__nextHasNoMarginBottom
						__next40pxDefaultPlacement
						value={ parseInt( attributes[ breakpoint ].opacity ) }
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'opacity',
								parseInt( newVal )
							)
						}
						min={ 0 }
						max={ 100 }
						step={ 5 }
						withInputField={ false }
						allowReset={ true }
						beforeIcon={
							<Icon
								icon={ cog }
								className={ `opacity-${ attributes[ breakpoint ].opacity }` }
							/>
						}
					/>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
