/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import { Icon, cog } from '@wordpress/icons';
import {
	CheckboxControl,
	RangeControl,
	Button,
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export function getClassName( attributes ) {
	return clsx(
		attributes.placements && {
			[ `${ attributes.placements.stack }` ]:
				attributes.placements.stack !== undefined &&
				attributes.placements.stack !== 'undefined',
		}
	);
}

export function PlacementsPanel( {
	attributes,
	setAttribute,
	supportedSettings,
} ) {
	const resetAll = () => {
		setAttribute( 'placements', 'stack', undefined );
	};

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Placements" resetAll={ resetAll }>
			<small className="text-muted" style={ titleStyle }>
				Configure component placements
			</small>
			{ supportedSettings.stack && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ 'placements' ].stack !== undefined
					}
					label={ __( 'Child placement' ) }
					onDeselect={ () =>
						setAttribute( 'placements', 'stack', undefined )
					}
				>
					<ToggleGroupControl
						label="Child Placement"
						value={ attributes.placements.stack }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setAttribute( 'placements', 'stack', newVal )
						}
					>
						<ToggleGroupControlOption
							value="hstack"
							label="Horizontal (row)"
						/>
						<ToggleGroupControlOption
							value="vstack"
							label="Vertical (stacked/column)"
						/>
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
