/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import { Icon, cog } from '@wordpress/icons';
import {
	CheckboxControl,
	RangeControl,
	Button,
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { SizeRangeControl } from '../bootstrap_sizes';
import { generateGapIcon } from '../svg_icons';

export function getClassName( attributes ) {
	return clsx(
		attributes.spacings && {
			[ `gap-${ attributes.spacings.gap }` ]:
				attributes.spacings.gap !== undefined &&
				attributes.spacings.gap !== 'undefined',
			[ `column-gap-${ attributes.spacings.column_gap }` ]:
				attributes.spacings.column_gap !== undefined &&
				attributes.spacings.column_gap !== 'undefined',
			[ `row-gap-${ attributes.spacings.row_gap }` ]:
				attributes.spacings.row_gap !== undefined &&
				attributes.spacings.row_gap !== 'undefined',
		}
	);
}

export function SpacingsPanel( {
	attributes,
	setAttribute,
	supportedSettings,
} ) {
	const resetAll = () => {
		setAttribute( 'spacings', 'gap', undefined );
		setAttribute( 'spacings', 'column_gap', undefined );
		setAttribute( 'spacings', 'row_gap', undefined );
	};

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Spacings" resetAll={ resetAll }>
			<small className="text-muted" style={ titleStyle }>
				Configure general spacings
			</small>

			{ supportedSettings.gaps && (
				<ToolsPanelItem
					hasValue={ () => attributes.spacings.gap !== undefined }
					label={ __(
						'Gaps between children for flex and grid layouts'
					) }
					onDeselect={ resetAll }
				>
					<SizeRangeControl
						breakpoint="spacings"
						type="gap"
						value={ attributes.spacings.gap }
						title={
							__( 'Horizontal and Vertical Gaps' ) +
							' (' +
							attributes.spacings.gap +
							')'
						}
						setBreakpointAttribute={ setAttribute }
						icon={ generateGapIcon( 'xy' ) }
					/>
					<SizeRangeControl
						breakpoint="spacings"
						type="column_gap"
						value={ attributes.spacings.column_gap }
						title={
							__( 'Vertical Gaps' ) +
							' (' +
							attributes.spacings.column_gap +
							')'
						}
						setBreakpointAttribute={ setAttribute }
						icon={ generateGapIcon( 'x' ) }
					/>
					<SizeRangeControl
						breakpoint="spacings"
						type="row_gap"
						value={ attributes.spacings.row_gap }
						title={
							__( 'Horizontal and Vertical Gaps' ) +
							' (' +
							attributes.spacings.row_gap +
							')'
						}
						setBreakpointAttribute={ setAttribute }
						icon={ generateGapIcon( 'y' ) }
					/>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
