/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import { Icon, cog } from '@wordpress/icons';
import {
	CheckboxControl,
	Button,
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { SizeRangeControl } from '../bootstrap_sizes';

export function getClassName( attributes ) {
	return clsx(
		attributes.border && {
			[ `rounded-${ attributes.border.type }` ]:
				attributes.border.type !== undefined &&
				attributes.border.type !== 'custom',
			[ `rounded-${ attributes.border.rounded }` ]:
				attributes.border.rounded !== undefined &&
				attributes.border.type !== 'custom',
			'rounded-top':
				attributes.border.type === 'custom' &&
				attributes.border.type_location.top &&
				attributes.border.rounded === undefined,
			'rounded-bottom':
				attributes.border.type === 'custom' &&
				attributes.border.type_location.bottom &&
				attributes.border.rounded === undefined,
			'rounded-start':
				attributes.border.type === 'custom' &&
				attributes.border.type_location.start &&
				attributes.border.rounded === undefined,
			'rounded-end':
				attributes.border.type === 'custom' &&
				attributes.border.type_location.end &&
				attributes.border.rounded === undefined,
			[ `rounded-top-${ attributes.border.rounded }` ]:
				attributes.border.type === 'custom' &&
				attributes.border.type_location.top &&
				attributes.border.rounded !== undefined,
			[ `rounded-bottom-${ attributes.border.rounded }` ]:
				attributes.border.type === 'custom' &&
				attributes.border.type_location.bottom &&
				attributes.border.rounded !== undefined,
			[ `rounded-start-${ attributes.border.rounded }` ]:
				attributes.border.type === 'custom' &&
				attributes.border.type_location.start &&
				attributes.border.rounded !== undefined,
			[ `rounded-end-${ attributes.border.rounded }` ]:
				attributes.border.type === 'custom' &&
				attributes.border.type_location.end &&
				attributes.border.rounded !== undefined,

			[ `border-opacity-${ attributes.border.opacity }` ]:
				attributes.border.opacity !== undefined &&
				! isNaN( attributes.border.opacity ),
			[ `border-${ attributes.border.color }` ]:
				attributes.border.color !== undefined,
			[ `border-${ attributes.border.width }` ]:
				attributes.border.width !== undefined &&
				! isNaN( attributes.border.width ),
			border:
				( attributes.border.width !== undefined &&
					! isNaN( attributes.border.width ) ) ||
				attributes.border.color !== undefined,
		}
	);
}

export function BorderPanel( { attributes, setAttribute, supportedSettings } ) {
	const resetRounded = () => {
		setAttribute( 'border', 'type', undefined );
		setAttribute( 'border', 'type_location', undefined );
		setAttribute( 'border', 'rounded', undefined );
	};

	const resetDisplay = () => {
		setAttribute( 'border', 'width', undefined );
		setAttribute( 'border', 'color', undefined );
		setAttribute( 'border', 'opacity', undefined );
	};

	const resetAll = () => {
		resetRounded();
		resetDisplay();
	};

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Border" resetAll={ resetAll }>
			<small className="text-muted" style={ titleStyle }>
				Configure component border and rounded settings
			</small>
			{ supportedSettings.rounded && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes.border.rounded !== undefined ||
						attributes.border.type !== undefined
					}
					label={ __( 'Rounded corners' ) }
					onDeselect={ () => resetRounded() }
				>
					<ToggleGroupControl
						label={ `Select border location` }
						value={ attributes.border.type }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setAttribute( 'border', 'type', newVal )
						}
					>
						<ToggleGroupControlOption value="rounded" label="all" />
						<ToggleGroupControlOption
							value="circle"
							label="circle"
						/>
						<ToggleGroupControlOption value="pill" label="pill" />
						<ToggleGroupControlOption
							value="custom"
							label="customize"
						/>
					</ToggleGroupControl>
					<Button
						variant="secondary"
						size="small"
						text="Reset"
						onClick={ () =>
							setAttribute( 'border', 'type', undefined )
						}
					/>
					{ attributes.border.type === 'custom' && (
						<>
							<CheckboxControl
								className="mt-3"
								__nextHasNoMarginBottom
								label="Round top"
								help="Round top corners"
								checked={ attributes.border.type_location.top }
								onChange={ ( newVal ) =>
									setAttribute( 'border', 'type_location', {
										...attributes.border.type_location,
										top: newVal,
									} )
								}
							/>
							<CheckboxControl
								__nextHasNoMarginBottom
								label="Round bottom"
								help="Round bottom corners"
								checked={
									attributes.border.type_location.bottom
								}
								onChange={ ( newVal ) =>
									setAttribute( 'border', 'type_location', {
										...attributes.border.type_location,
										bottom: newVal,
									} )
								}
							/>
							<CheckboxControl
								__nextHasNoMarginBottom
								label="Round left"
								help="Round left side corners"
								checked={
									attributes.border.type_location.start
								}
								onChange={ ( newVal ) =>
									setAttribute( 'border', 'type_location', {
										...attributes.border.type_location,
										start: newVal,
									} )
								}
							/>
							<CheckboxControl
								__nextHasNoMarginBottom
								label="Round right"
								help="Round right side corners"
								checked={ attributes.border.type_location.end }
								onChange={ ( newVal ) =>
									setAttribute( 'border', 'type_location', {
										...attributes.border.type_location,
										end: newVal,
									} )
								}
							/>
						</>
					) }

					<SizeRangeControl
						breakpoint="border"
						type="rounded"
						value={ attributes.border.rounded }
						title={
							__( 'Rounded Size' ) +
							' (' +
							attributes.border.rounded +
							')'
						}
						setBreakpointAttribute={ setAttribute }
						icon={
							<Icon
								icon={ cog }
								className={ `rounded-${ attributes.border.rounded }` }
							/>
						}
					/>
				</ToolsPanelItem>
			) }
			{ supportedSettings.display && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes.border.opacity !== undefined ||
						attributes.border.color !== undefined ||
						attributes.border.width !== undefined
					}
					label={ __( 'Border Display' ) }
					onDeselect={ () => resetDisplay() }
				>
					<ToggleGroupControl
						label={ `Select border width` }
						value={ attributes.border.width }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setAttribute( 'border', 'width', newVal )
						}
					>
						<ToggleGroupControlOption value="1" label="1" />
						<ToggleGroupControlOption value="2" label="2" />
						<ToggleGroupControlOption value="3" label="3" />
						<ToggleGroupControlOption value="4" label="4" />
						<ToggleGroupControlOption value="5" label="5" />
					</ToggleGroupControl>
					<Button
						className="mb-3"
						variant="secondary"
						size="small"
						text="Reset"
						onClick={ () =>
							setAttribute( 'border', 'width', undefined )
						}
					/>

					<ToggleGroupControl
						label={ `Select border color` }
						value={ attributes.border.color }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setAttribute( 'border', 'color', newVal )
						}
					>
						{ [
							'primary',
							'secondary',
							'tertiary',
							'success',
							'danger',
							'warning',
							'info',
							'light',
							'dark',
						].map( ( item ) => {
							return (
								<ToggleGroupControlOption
									key={ item }
									className={ `bg-${ item }` }
									value={ item }
									label=" "
								/>
							);
						} ) }
					</ToggleGroupControl>
					<Button
						className="mb-3"
						variant="secondary"
						size="small"
						text="Reset"
						onClick={ () =>
							setAttribute( 'border', 'color', undefined )
						}
					/>

					<ToggleGroupControl
						label={ `Select border opacity` }
						value={ attributes.border.opacity }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setAttribute( 'border', 'opacity', newVal )
						}
					>
						<ToggleGroupControlOption
							value="undefined"
							label="100%"
						/>
						<ToggleGroupControlOption value="75" label="75%" />
						<ToggleGroupControlOption value="50" label="50%" />
						<ToggleGroupControlOption value="25" label="25%" />
						<ToggleGroupControlOption value="10" label="10%" />
					</ToggleGroupControl>
					<Button
						variant="secondary"
						size="small"
						text="Reset"
						onClick={ () =>
							setAttribute( 'border', 'opacity', undefined )
						}
					/>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
