/**
 * WordPress dependencies
 */
import { RangeControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

// Define available Bootstrap spacing options
export const bootstrapSizes = [
	{ name: 'None', value: 0, label: 'None' },
	{ name: 'Extra small', value: 1, label: 'XS' },
	{ name: 'Small', value: 2, label: 'S' },
	{ name: 'Medium', value: 3, label: 'M' },
	{ name: 'Large', value: 4, label: 'L' },
	{ name: 'Extra large', value: 5, label: 'XL' },
];

export const SizeRangeControl = ( {
	breakpoint,
	type,
	value,
	title,
	icon,
	setBreakpointAttribute,
} ) => {
	//	const Icon = () => generateSpatialIcon(type, side);
	return (
		<RangeControl
			className="mt-3"
			__nextHasNoMarginBottom
			__next40pxDefaultSize
			value={ isNaN( parseInt( value ) ) ? undefined : parseInt( value ) }
			onChange={ ( newVal ) =>
				setBreakpointAttribute(
					breakpoint,
					type,
					isNaN( parseInt( newVal ) ) ? undefined : parseInt( newVal )
				)
			}
			withInputField={ false }
			aria-valuenow={ value }
			aria-valuetext={ __( title, 'peaches' ) }
			label={ __( title, 'peaches' ) }
			initialPosition={ 0 }
			min={ 0 }
			max={ 5 }
			step={ 1 }
			marks={ bootstrapSizes }
			renderTooltipContent={ ( value ) => bootstrapSizes[ value ].name }
			allowReset={ true }
			beforeIcon={ icon }
		/>
	);
};
