/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import {
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	ToggleControl,
	SVG,
	Path,
} from '@wordpress/components';
import { useMemo, useCallback, useState, useEffect } from '@wordpress/element';
import { __ } from '@wordpress/i18n';

/**
 * Internal dependencies
 */
import { SizeRangeControl } from '../bootstrap_sizes';
import { generateSpatialIcon } from '../svg_icons';

export function getClassName( attributes, type ) {
	const XY = `${ type }XY`;

	const classPrefix = Array.from( type )[ 0 ];

	return clsx(
		attributes.xs && {
			[ `${ classPrefix }-${ attributes.xs[ XY ] }` ]:
				attributes.xs[ XY ] !== undefined &&
				! isNaN( attributes.xs[ XY ] ),
		},
		attributes.xs &&
			[ 'y', 'x', 't', 'b', 's', 'e' ].map( ( side ) => {
				return (
					attributes.xs && {
						[ `${ classPrefix }${ side }-${
							attributes.xs[ type + side.toUpperCase() ]
						}` ]:
							attributes.xs[ type + side.toUpperCase() ] !==
								undefined &&
							! isNaN(
								attributes.xs[ type + side.toUpperCase() ]
							),
					}
				);
			} ),
		[ 'sm', 'md', 'lg', 'xl', 'xxl' ].map( ( val ) => {
			return attributes[ val ]
				? {
						[ `${ classPrefix }-${ val }-${ attributes[ val ][ XY ] }` ]:
							attributes[ val ][ XY ] !== undefined &&
							! isNaN( attributes[ val ][ XY ] ),
				  }
				: {};
		} ),
		[ 'sm', 'md', 'lg', 'xl', 'xxl' ].map( ( val ) => {
			if ( attributes[ val ] ) {
				return [ 'y', 'x', 't', 'b', 's', 'e' ].map( ( side ) => {
					return {
						[ `${ classPrefix }${ side }-${ val }-${
							attributes[ val ][ type + side.toUpperCase() ]
						}` ]:
							attributes[ val ][ type + side.toUpperCase() ] !==
								undefined &&
							! isNaN(
								attributes[ val ][ type + side.toUpperCase() ]
							),
					};
				} );
			}
			return {};
		} )
	);
}

function BootstrapSpacingPanel( {
	breakpoint,
	attributes,
	setBreakpointAttribute,
	type,
	title,
	hasAdvancedMode,
} ) {
	const XY = `${ type }XY`;
	const X = `${ type }X`;
	const Y = `${ type }Y`;
	const T = `${ type }T`;
	const B = `${ type }B`;
	const S = `${ type }S`;
	const E = `${ type }E`;

	const valueT = useMemo( () => {
		return hasAdvancedMode && attributes[ breakpoint ]
			? attributes[ breakpoint ][ T ]
			: undefined;
	}, [ attributes, breakpoint, T, hasAdvancedMode ] );

	const valueB = useMemo( () => {
		return hasAdvancedMode && attributes[ breakpoint ]
			? attributes[ breakpoint ][ B ]
			: undefined;
	}, [ attributes, breakpoint, B, hasAdvancedMode ] );

	const valueS = useMemo( () => {
		return hasAdvancedMode && attributes[ breakpoint ]
			? attributes[ breakpoint ][ S ]
			: undefined;
	}, [ attributes, breakpoint, S, hasAdvancedMode ] );

	const valueE = useMemo( () => {
		return hasAdvancedMode && attributes[ breakpoint ]
			? attributes[ breakpoint ][ E ]
			: undefined;
	}, [ attributes, breakpoint, E, hasAdvancedMode ] );

	// State for linked vs unlinked controls
	const [ isAdvancedMode, setAdvancedMode ] = useState( () => {
		// Initialize with a function to check if any advanced values are set
		return !! ( valueT || valueB || valueS || valueE );
	} );

	useEffect( () => {
		// Check if any individual side values exist and update advanced mode accordingly
		const hasIndividualValues = !! ( valueT || valueB || valueS || valueE );
		if ( hasIndividualValues && ! isAdvancedMode ) {
			setAdvancedMode( true );
		}
	}, [ valueT, valueB, valueS, valueE, isAdvancedMode ] );

	return (
		<>
			{ title }
			<div className="mt-3">
				<ToggleControl
					__nextHasNoMarginBottom
					label={ `Advanced ${ type } Controls` }
					help={
						isAdvancedMode
							? 'Individual controls for each side'
							: 'Simplified controls with linked values'
					}
					checked={ isAdvancedMode }
					onChange={ () => setAdvancedMode( ! isAdvancedMode ) }
				/>
			</div>
			<SizeRangeControl
				breakpoint={ breakpoint }
				type={ XY }
				value={ attributes[ breakpoint ][ XY ] }
				title={ `${ type }s all sides` }
				setBreakpointAttribute={ setBreakpointAttribute }
				icon={ generateSpatialIcon( type, 'XY' ) }
			/>
			<SizeRangeControl
				breakpoint={ breakpoint }
				type={ X }
				value={ attributes[ breakpoint ][ X ] }
				title={ `${ type }s left and right sides` }
				setBreakpointAttribute={ setBreakpointAttribute }
				icon={ generateSpatialIcon( type, 'X' ) }
			/>
			<SizeRangeControl
				breakpoint={ breakpoint }
				type={ Y }
				value={ attributes[ breakpoint ][ Y ] }
				title={ `${ type }s top and bottom sides` }
				setBreakpointAttribute={ setBreakpointAttribute }
				icon={ generateSpatialIcon( type, 'Y' ) }
			/>
			{ hasAdvancedMode && isAdvancedMode && (
				<>
					<SizeRangeControl
						breakpoint={ breakpoint }
						type={ T }
						value={ attributes[ breakpoint ][ T ] }
						title={ `${ type } top` }
						setBreakpointAttribute={ setBreakpointAttribute }
						icon={ generateSpatialIcon( type, 'T' ) }
					/>
					<SizeRangeControl
						breakpoint={ breakpoint }
						type={ B }
						value={ attributes[ breakpoint ][ B ] }
						title={ `${ type } bottom` }
						setBreakpointAttribute={ setBreakpointAttribute }
						icon={ generateSpatialIcon( type, 'B' ) }
					/>
					<SizeRangeControl
						breakpoint={ breakpoint }
						type={ S }
						value={ attributes[ breakpoint ][ S ] }
						title={ `${ type } left` }
						setBreakpointAttribute={ setBreakpointAttribute }
						icon={ generateSpatialIcon( type, 'S' ) }
					/>
					<SizeRangeControl
						breakpoint={ breakpoint }
						type={ E }
						value={ attributes[ breakpoint ][ E ] }
						title={ `${ type } right` }
						setBreakpointAttribute={ setBreakpointAttribute }
						icon={ generateSpatialIcon( type, 'E' ) }
					/>
				</>
			) }
		</>
	);
}

export function SpacingsPanel( {
	breakpoint,
	attributes,
	setBreakpointAttribute,
	supportedSettings,
} ) {
	const resetGlobal = ( type ) => {
		[ 'XY', 'X', 'Y' ].forEach( ( side ) => {
			setBreakpointAttribute(
				breakpoint,
				`${ type }${ side }`,
				undefined
			);
		} );
	};
	const resetAdvanced = ( type ) => {
		[ 'T', 'B', 'E', 'S' ].forEach( ( side ) => {
			setBreakpointAttribute(
				breakpoint,
				`${ type }${ side }`,
				undefined
			);
		} );
	};
	const resetAll = () => {
		[ 'padding', 'margin', 'gutter' ].forEach( ( type ) =>
			resetGlobal( type )
		);
		[ 'padding', 'margin' ].forEach( ( type ) => resetAdvanced( type ) );
	};

	const hasValue = useCallback(
		( type ) => {
			return (
				attributes[ breakpoint ][ `${ type }XY` ] ||
				attributes[ breakpoint ][ `${ type }Y` ] ||
				attributes[ breakpoint ][ `${ type }X` ] ||
				( ( type === 'padding' || type === 'margin' ) &&
					( attributes[ breakpoint ][ `${ type }T` ] ||
						attributes[ breakpoint ][ `${ type }B` ] ||
						attributes[ breakpoint ][ `${ type }E` ] ||
						attributes[ breakpoint ][ `${ type }S` ] ) )
			);
		},
		[ attributes, breakpoint ]
	);

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Spacings" resetAll={ resetAll }>
			{ supportedSettings.gutter && (
				<ToolsPanelItem
					hasValue={ () => hasValue( 'gutter' ) }
					label={ __( 'Gutters' ) }
					onDeselect={ () => {
						resetGlobal( 'gutter' );
						resetAdvanced( 'gutter' );
					} }
				>
					<small className="text-muted" style={ titleStyle }>
						Configure component gutter
					</small>
					<BootstrapSpacingPanel
						hasAdvancedMode={ false }
						setBreakpointAttribute={ setBreakpointAttribute }
						attributes={ attributes }
						breakpoint={ breakpoint }
						type="gutter"
						title={
							<div className="hstack align-items-start">
								<SVG
									xmlns="http://www.w3.org/2000/svg"
									width="100%"
									height="auto"
									viewBox="0 0 32 32"
								>
									<Path
										id="rect1"
										stroke="#000000"
										fill="none"
										strokeWidth="0.5"
										strokeDasharray="0.3, 2.4"
										d="M 0.14408029,0.14408029 H 28.960087 V 28.960087 H 0.14408029 Z"
									/>
									<Path
										id="rect1-5"
										stroke="#000000"
										fill="none"
										strokeWidth="0.2"
										d="M 2.6982258,2.6982258 H 13.176773 V 13.176774 H 2.6982258 Z"
									/>
									<Path
										id="rect1-5-9"
										stroke="#000000"
										fill="none"
										strokeWidth="0.2"
										d="M 15.927393,2.6982258 H 26.40594 V 13.176774 H 15.927393 Z"
									/>
									<Path
										id="rect1-5-93"
										stroke="#000000"
										fill="none"
										strokeWidth="0.2"
										d="M 15.927393,15.927393 H 26.40594 V 26.405941 H 15.927393 Z"
									/>
									<Path
										id="rect1-5-9-0"
										stroke="#000000"
										fill="none"
										strokeWidth="0.2"
										d="M 2.6982258,15.927393 H 13.176773 V 26.405941 H 2.6982258 Z"
									/>
								</SVG>
								Select spacing to be used between the cells in
								the row.
							</div>
						}
					/>
				</ToolsPanelItem>
			) }
			{ supportedSettings.margin && (
				<ToolsPanelItem
					hasValue={ () => hasValue( 'margin' ) }
					label={ __( 'Margins' ) }
					onDeselect={ () => {
						resetGlobal( 'margin' );
						resetAdvanced( 'margin' );
					} }
				>
					<small className="text-muted" style={ titleStyle }>
						Configure component margin
					</small>
					<BootstrapSpacingPanel
						hasAdvancedMode={ true }
						setBreakpointAttribute={ setBreakpointAttribute }
						attributes={ attributes }
						breakpoint={ breakpoint }
						type="margin"
						title={
							<div className="hstack align-items-start">
								<SVG
									xmlns="http://www.w3.org/2000/svg"
									width="100%"
									height="auto"
									viewBox="0 0 40 40"
								>
									<Path
										id="rect1"
										stroke="#000000"
										fill="none"
										strokeWidth="0.5"
										strokeDasharray="0.3, 2.4"
										d="M 0.1485149,0.1485149 H 29.851485 V 29.851485 H 0.1485149 Z"
									/>
									<Path
										id="rect1-5"
										stroke="#000000"
										fill="none"
										strokeWidth="0.2"
										d="M 5.0990097,5.0990097 H 24.90099 V 24.90099 H 5.0990097 Z"
									/>
								</SVG>
								Select margin (space outside the element) to be
								used.
							</div>
						}
					/>
				</ToolsPanelItem>
			) }
			{ supportedSettings.padding && (
				<ToolsPanelItem
					hasValue={ () => hasValue( 'padding' ) }
					label={ __( 'Paddings' ) }
					onDeselect={ () => {
						resetGlobal( 'padding' );
						resetAdvanced( 'padding' );
					} }
				>
					<small className="text-muted" style={ titleStyle }>
						Configure component padding
					</small>
					<BootstrapSpacingPanel
						hasAdvancedMode={ true }
						setBreakpointAttribute={ setBreakpointAttribute }
						attributes={ attributes }
						breakpoint={ breakpoint }
						type="padding"
						title={
							<div className="hstack align-items-start">
								<SVG
									xmlns="http://www.w3.org/2000/svg"
									width="100%"
									height="auto"
									viewBox="0 0 40 40"
								>
									<Path
										id="rect1"
										stroke="#000000"
										fill="none"
										strokeWidth="0.2"
										d="M 0.1485149,0.1485149 H 29.851485 V 29.851485 H 0.1485149 Z"
									/>
									<Path
										id="rect1-5"
										stroke="#000000"
										fill="none"
										strokeWidth="0.5"
										strokeDasharray="0.3, 2.4"
										d="M 5.0990097,5.0990097 H 24.90099 V 24.90099 H 5.0990097 Z"
									/>
								</SVG>
								Select padding (space inside the element) to be
								used.
							</div>
						}
					/>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
