/**
 * Generates SVG icons for Bootstrap padding and margin settings
 * @param {string} type - 'padding' or 'margin'
 * @param {string} side - 'xy', 'x', 'y', 's', 'e', 't', 'b'
 * @return {JSX.Element} - SVG icon component
 */
export const generateSpatialIcon = ( type, side ) => {
	// Handle gutter icons differently
	if ( type === 'gutter' ) {
		return generateGapIcon( side );
	}

	const isMargin = type === 'margin';

	// Base rectangle positions
	const outer = { x: 3, y: 3, width: 18, height: 18, rx: 2 };
	const inner = { x: 7, y: 7, width: 10, height: 10, rx: 1 };

	// Determine which rectangle to use as base (with stroke)
	// For padding: outer rectangle, for margin: inner rectangle
	const baseRect = isMargin ? inner : outer;

	// Highlight areas
	const highlights = [];

	// For XY case, draw all sides
	if ( side === 'XY' ) {
		if ( isMargin ) {
			// For margin xy - highlight all around the inner rectangle
			highlights.push(
				<rect
					key="margin-xy-outer"
					x={ inner.x - 2 }
					y={ inner.y - 2 }
					width={ inner.width + 4 }
					height={ inner.height + 4 }
					rx={ inner.rx + 1 }
					stroke="currentColor"
					strokeWidth="4"
					strokeOpacity="0.3"
					fill="none"
				/>
			);
		} else {
			// For padding xy - highlight inside the outer rectangle
			highlights.push(
				<rect
					key="padding-xy-inner"
					x={ inner.x - 2 }
					y={ inner.y - 2 }
					width={ inner.width + 4 }
					height={ inner.height + 4 }
					rx={ inner.rx + 1 }
					stroke="currentColor"
					strokeWidth="4"
					strokeOpacity="0.3"
					fill="none"
				/>
			);
		}
	} else {
		// For directional cases (x, y, t, b, s, e)
		// Map the side to specific edges
		const sidesToEdges = {
			X: [ 'left', 'right' ],
			Y: [ 'top', 'bottom' ],
			S: [ 'left' ],
			E: [ 'right' ],
			T: [ 'top' ],
			B: [ 'bottom' ],
		};

		const edges = sidesToEdges[ side ] || [];

		// Draw specific edges
		edges.forEach( ( edge ) => {
			if ( edge === 'left' ) {
				highlights.push(
					<path
						key="left-edge"
						d={
							isMargin
								? `M${ inner.x - 2 },${ inner.y } L${
										inner.x - 2
								  },${ inner.y + inner.height }`
								: `M${ outer.x + 4 },${ inner.y } L${
										outer.x + 4
								  },${ inner.y + inner.height }`
						}
						stroke="currentColor"
						strokeWidth="4"
						strokeOpacity="0.3"
					/>
				);
			} else if ( edge === 'right' ) {
				highlights.push(
					<path
						key="right-edge"
						d={
							isMargin
								? `M${ inner.x + inner.width + 2 },${
										inner.y
								  } L${ inner.x + inner.width + 2 },${
										inner.y + inner.height
								  }`
								: `M${ outer.x + outer.width - 4 },${
										inner.y
								  } L${ outer.x + outer.width - 4 },${
										inner.y + inner.height
								  }`
						}
						stroke="currentColor"
						strokeWidth="4"
						strokeOpacity="0.3"
					/>
				);
			} else if ( edge === 'top' ) {
				highlights.push(
					<path
						key="top-edge"
						d={
							isMargin
								? `M${ inner.x },${ inner.y - 2 } L${
										inner.x + inner.width
								  },${ inner.y - 2 }`
								: `M${ inner.x },${ outer.y + 4 } L${
										inner.x + inner.width
								  },${ outer.y + 4 }`
						}
						stroke="currentColor"
						strokeWidth="4"
						strokeOpacity="0.3"
					/>
				);
			} else if ( edge === 'bottom' ) {
				highlights.push(
					<path
						key="bottom-edge"
						d={
							isMargin
								? `M${ inner.x },${
										inner.y + inner.height + 2
								  } L${ inner.x + inner.width },${
										inner.y + inner.height + 2
								  }`
								: `M${ inner.x },${
										outer.y + outer.height - 4
								  } L${ inner.x + inner.width },${
										outer.y + outer.height - 4
								  }`
						}
						stroke="currentColor"
						strokeWidth="4"
						strokeOpacity="0.3"
					/>
				);
			}
		} );
	}

	return (
		<svg
			width="24"
			height="24"
			viewBox="0 0 24 24"
			xmlns="http://www.w3.org/2000/svg"
		>
			<rect
				x={ baseRect.x }
				y={ baseRect.y }
				width={ baseRect.width }
				height={ baseRect.height }
				rx={ baseRect.rx }
				stroke="currentColor"
				strokeWidth="1"
				fill="none"
			/>
			{ highlights }
		</svg>
	);
};

