/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import { TabPanel, Panel, PanelBody } from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';

/**
 * Internal dependencies
 */
import {
	SpacingsPanel,
	getClassName as getSpacingsClassName,
} from './spacings_panel.js';
import {
	SizesPanel,
	getClassName as getSizesClassName,
} from './sizes_panel.js';
import {
	DisplayPanel,
	getClassName as getDisplayClassName,
} from './display_panel.js';
import {
	PlacementsPanel,
	getClassName as getPlacementsClassName,
} from './placements_panel.js';
import {
	CarouselPanel,
	getDataValues as getCarouselDataValues,
} from './carousel_panel.js';

export function computeClassName( attributes ) {
	return clsx(
		getSizesClassName( attributes ),
		getPlacementsClassName( attributes ),
		getDisplayClassName( attributes ),
		getSpacingsClassName( attributes, 'gutter' ),
		getSpacingsClassName( attributes, 'margin' ),
		getSpacingsClassName( attributes, 'padding' )
	);
}
export function computeDataValues( attributes ) {
	return getCarouselDataValues( attributes );
}
export function tabInitValues(
	attributes,
	breakpoint,
	supportedSettings,
	groupName
) {
	let tabHasSettings = false;
	if ( groupName ) {
		tabHasSettings = hasGroupSettingInBreakpoint(
			attributes,
			breakpoint,
			groupName,
			supportedSettings
		);
	} else {
		tabHasSettings = checkAllGroupsForBreakpoint(
			attributes,
			breakpoint,
			supportedSettings
		);
	}

	return {
		name: breakpoint,
		title: breakpoint.toUpperCase(),
		className: clsx( 'col btn btn-outline-secondary btn-sm', {
			'btn-light': tabHasSettings,
		} ),
		settings: attributes[ breakpoint ],
	};
}

// Breakpoints as defined in Bootstrap
export const breakpoints = [ 'xs', 'sm', 'md', 'lg', 'xl', 'xxl' ];

function hasGroupSettingInBreakpoint(
	attributes,
	breakpoint,
	groupName,
	supportedSettings
) {
	// If the breakpoint doesn't exist or is an empty array, return false
	if (
		! attributes[ breakpoint ] ||
		( Array.isArray( attributes[ breakpoint ] ) &&
			attributes[ breakpoint ].length === 0 )
	) {
		return false;
	}

	// Get the breakpoint settings
	const breakpointSettings = attributes[ breakpoint ];

	// Get the settings in the specified group
	const groupSettings = supportedSettings[ groupName ];

	// Check if any setting in the breakpoint matches a setting in the group
	for ( const key in breakpointSettings ) {
		// Check if the key starts with any of the supported settings in the group
		for ( const settingKey in groupSettings ) {
			if (
				groupSettings[ settingKey ] &&
				key.toLowerCase().startsWith( settingKey.toLowerCase() )
			) {
				return true;
			}
		}
	}

	return false;
}

// Function to check all groups for a specific breakpoint
function checkAllGroupsForBreakpoint(
	attributes,
	breakpoint,
	supportedSettings
) {
	for ( const groupName in supportedSettings ) {
		if (
			hasGroupSettingInBreakpoint(
				attributes,
				breakpoint,
				groupName,
				supportedSettings
			)
		) {
			return true;
		}
	}

	return false;
}

export function ResponsiveTabPanel( {
	setAttributes,
	attributes,
	supportedSettings,
	type,
} ) {
	const [ tabs, setTabs ] = useState(
		breakpoints.map( ( breakpoint ) =>
			tabInitValues( attributes, breakpoint, supportedSettings, type )
		)
	);

	const setBreakpointAttribute = ( breakpoint, key, value ) => {
		setAttributes( {
			[ breakpoint ]: {
				...attributes[ breakpoint ],
				[ key ]: value,
			},
		} );

		setTabs(
			breakpoints.map( ( initBreakpoint ) =>
				tabInitValues(
					attributes,
					initBreakpoint,
					supportedSettings,
					type
				)
			)
		);
	};

	useEffect( () => {
		const tabObserver = new MutationObserver(
			( mutationsList, observer ) => {
				const tabList = document.querySelector(
					'.components-tab-panel__tabs'
				);
				if ( tabList ) {
					tabList.classList.add( 'btn-group' );
					tabList.classList.add( 'd-flex' );

					const buttons =
						tabList.querySelectorAll( '.components-button' );
					buttons.forEach( ( button ) =>
						button.classList.remove( 'components-button' )
					);

					observer.disconnect();
				}
			}
		);

		tabObserver.observe( document.body, {
			childList: true,
			subtree: true,
		} );

		return () => tabObserver.disconnect();
	}, [] );

	return (
		<Panel>
			<PanelBody
				title={ 'Responsive ' + ( type ? type : 'settings' ) }
				initialOpen={ false }
			>
				<TabPanel activeClass="active" tabs={ tabs }>
					{ ( tab ) => {
						return (
							<>
								{ ( type === 'carousel' ||
									supportedSettings.carousel ) && (
									<>
										<CarouselPanel
											setBreakpointAttribute={
												setBreakpointAttribute
											}
											attributes={ attributes }
											breakpoint={ tab.name }
										/>
									</>
								) }
								{ ( type === 'spacings' ||
									supportedSettings.spacings ) && (
									<>
										<SpacingsPanel
											setBreakpointAttribute={
												setBreakpointAttribute
											}
											attributes={ attributes }
											breakpoint={ tab.name }
											supportedSettings={
												supportedSettings.spacings
											}
										/>
									</>
								) }
								{ ( type === 'sizes' ||
									supportedSettings.sizes ) && (
									<>
										<SizesPanel
											setBreakpointAttribute={
												setBreakpointAttribute
											}
											attributes={ attributes }
											breakpoint={ tab.name }
											supportedSettings={
												supportedSettings.sizes
											}
										/>
									</>
								) }
								{ ( type === 'placements' ||
									supportedSettings.placements ) && (
									<>
										<PlacementsPanel
											setBreakpointAttribute={
												setBreakpointAttribute
											}
											attributes={ attributes }
											breakpoint={ tab.name }
											supportedSettings={
												supportedSettings.placements
											}
										/>
									</>
								) }
								{ ( type === 'display' ||
									supportedSettings.display ) && (
									<>
										<DisplayPanel
											setBreakpointAttribute={
												setBreakpointAttribute
											}
											attributes={ attributes }
											breakpoint={ tab.name }
											supportedSettings={
												supportedSettings.display
											}
										/>
									</>
								) }
							</>
						);
					} }
				</TabPanel>
			</PanelBody>
		</Panel>
	);
}
