/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import {
	RangeControl,
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export function getClassName( attributes ) {
	return clsx(
		attributes.xs && {
			[ `order-${ attributes.xs.colOrder }` ]:
				attributes.xs.colOrder !== undefined,
			[ `offset-${ attributes.xs.colOffset }` ]:
				attributes.xs.colOffset !== undefined &&
				! isNaN( attributes.xs.colOffset ),
			[ `z-${ attributes.xs.zIndex < 0 ? 'n1' : attributes.xs.zIndex }` ]:
				attributes.xs.zIndex !== undefined &&
				! isNaN( attributes.xs.zIndex ),
			[ `text-${ attributes.xs.textAlign }` ]:
				attributes.xs.textAlign !== undefined,
			[ `justify-content-${ attributes.xs.justifyContent }` ]:
				attributes.xs.justifyContent !== undefined,
			[ `align-items-${ attributes.xs.alignItems }` ]:
				attributes.xs.alignItems !== undefined,
			[ `align-self-${ attributes.xs.alignSelf }` ]:
				attributes.xs.alignSelf !== undefined,
		},
		[ 'sm', 'md', 'lg', 'xl', 'xxl' ].map( ( val ) => {
			return attributes[ val ]
				? {
						[ `order-${ val }-${ attributes[ val ].colOrder }` ]:
							attributes[ val ].colOrder !== undefined,
						[ `offset-${ val }-${ attributes[ val ].colOffset }` ]:
							attributes[ val ].colOffset !== undefined &&
							! isNaN( attributes[ val ].colOffset ),
						[ `z-${ val }-${
							attributes[ val ].zIndex < 0
								? 'n1'
								: attributes[ val ].zIndex
						}` ]:
							attributes[ val ].zIndex !== undefined &&
							! isNaN( attributes[ val ].zIndex ),
						[ `text-${ val }-${ attributes[ val ].textAlign }` ]:
							attributes[ val ].textAlign !== undefined,
						[ `justify-content-${ val }-${ attributes[ val ].justifyContent }` ]:
							attributes[ val ].justifyContent !== undefined,
						[ `align-items-${ val }-${ attributes[ val ].alignItems }` ]:
							attributes[ val ].alignItems !== undefined,
						[ `align-self-${ val }-${ attributes[ val ].alignSelf }` ]:
							attributes[ val ].alignSelf !== undefined,
				  }
				: {};
		} )
	);
}

export function PlacementsPanel( {
	breakpoint,
	attributes,
	setBreakpointAttribute,
	supportedSettings,
} ) {
	const resetAll = () => {
		setBreakpointAttribute( breakpoint, 'colOrder', undefined );
		setBreakpointAttribute( breakpoint, 'offset', undefined );
	};

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Placements" resetAll={ resetAll }>
			<small className="text-muted" style={ titleStyle }>
				Configure component placement and alignments
			</small>
			{ supportedSettings.colOrder && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].colOrder !== undefined
					}
					label={ __( 'Column Order' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'colOrder',
							undefined
						)
					}
				>
					<ToggleGroupControl
						label="Column Order"
						value={ attributes[ breakpoint ].colOrder }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'colOrder',
								newVal
							)
						}
					>
						<ToggleGroupControlOption value="first" label="First" />
						<ToggleGroupControlOption
							value="custom"
							label="Custom"
						/>
						<ToggleGroupControlOption value="last" label="Last" />
					</ToggleGroupControl>
					{ attributes[ breakpoint ].colOrder &&
						( attributes[ breakpoint ].colOrder === 'custom' ||
							! isNaN( attributes[ breakpoint ].colOrder ) ) && (
							<RangeControl
								__nextHasNoMarginBottom
								__next40pxDefaultPlacement
								className="mt-3"
								label={
									__( 'Column Order' ) +
									' (' +
									attributes[ breakpoint ].colOrder +
									')'
								}
								value={ parseInt(
									attributes[ breakpoint ].colOrder
								) }
								onChange={ ( newVal ) =>
									setBreakpointAttribute(
										breakpoint,
										'colOrder',
										isNaN( newVal )
											? undefined
											: parseInt( newVal )
									)
								}
								min={ 1 }
								max={ 5 }
								withInputField={ false }
								allowReset={ true }
							/>
						) }
				</ToolsPanelItem>
			) }
			{ supportedSettings.colOffset && (
				<ToolsPanelItem
					hasValue={ () =>
						! isNaN( attributes[ breakpoint ].colOffset )
					}
					label={ __( 'Column Offset' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'colOffset',
							undefined
						)
					}
				>
					<RangeControl
						__nextHasNoMarginBottom
						__next40pxDefaultPlacement
						label={
							__( 'Column Offset' ) +
							' (' +
							attributes[ breakpoint ].colOffset +
							')'
						}
						value={ parseInt( attributes[ breakpoint ].colOffset ) }
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'colOffset',
								isNaN( newVal ) ? undefined : parseInt( newVal )
							)
						}
						min={ 1 }
						max={ 12 }
						withInputField={ false }
						allowReset={ true }
					/>
				</ToolsPanelItem>
			) }
			{ supportedSettings.zIndex && (
				<ToolsPanelItem
					hasValue={ () =>
						! isNaN( attributes[ breakpoint ].zIndex )
					}
					label={ __( 'Stack placement (zIndex)' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'zIndex',
							undefined
						)
					}
				>
					<RangeControl
						__nextHasNoMarginBottom
						__next40pxDefaultPlacement
						label={
							__( 'Stack placement (zIndex)' ) +
							' (' +
							attributes[ breakpoint ].zIndex +
							')'
						}
						value={ parseInt( attributes[ breakpoint ].zIndex ) }
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'zIndex',
								isNaN( newVal ) ? undefined : parseInt( newVal )
							)
						}
						min={ -1 }
						max={ 3 }
						withInputField={ false }
						allowReset={ true }
					/>
				</ToolsPanelItem>
			) }
			{ supportedSettings.textAlign && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].textAlign !== undefined
					}
					label={ __( 'Text Alignment' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'textAlign',
							undefined
						)
					}
				>
					<ToggleGroupControl
						label="Text Alignment"
						value={ attributes[ breakpoint ].textAlign }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'textAlign',
								newVal
							)
						}
					>
						<ToggleGroupControlOption
							value="start"
							label="Start (default)"
						/>
						<ToggleGroupControlOption
							value="center"
							label="Centered"
						/>
						<ToggleGroupControlOption value="end" label="End" />
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
			{ supportedSettings.justifyContent && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].justifyContent !== undefined
					}
					label={ __( 'Content Justification (vertical)' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'justifyContent',
							undefined
						)
					}
				>
					<ToggleGroupControl
						label="Content Justification (vertical)"
						value={ attributes[ breakpoint ].justifyContent }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'justifyContent',
								newVal
							)
						}
					>
						<ToggleGroupControlOption
							value="start"
							label="Start (default)"
						/>
						<ToggleGroupControlOption
							value="center"
							label="Centered"
						/>
						<ToggleGroupControlOption value="end" label="End" />
						<ToggleGroupControlOption
							value="between"
							label="Between"
						/>
						<ToggleGroupControlOption
							value="around"
							label="Around"
						/>
						<ToggleGroupControlOption
							value="evenly"
							label="Evenly"
						/>
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
			{ supportedSettings.alignItems && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].alignItems !== undefined
					}
					label={ __( 'Content Alignment (horizontal)' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'alignItems',
							undefined
						)
					}
				>
					<ToggleGroupControl
						label="Content Alignment (horizontal)"
						value={ attributes[ breakpoint ].alignItems }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'alignItems',
								newVal
							)
						}
					>
						<ToggleGroupControlOption value="start" label="Start" />
						<ToggleGroupControlOption
							value="center"
							label="Centered"
						/>
						<ToggleGroupControlOption value="end" label="End" />
						<ToggleGroupControlOption
							value="baseline"
							label="Baseline"
						/>
						<ToggleGroupControlOption
							value="stretch"
							label="Stretch (default)"
						/>
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
			{ supportedSettings.alignSelf && (
				<ToolsPanelItem
					hasValue={ () =>
						attributes[ breakpoint ].alignSelf !== undefined
					}
					label={ __( 'Self Alignment (vertical)' ) }
					onDeselect={ () =>
						setBreakpointAttribute(
							breakpoint,
							'alignSelf',
							undefined
						)
					}
				>
					<ToggleGroupControl
						label="Self Alignment (vertical)"
						value={ attributes[ breakpoint ].alignSelf }
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
						onChange={ ( newVal ) =>
							setBreakpointAttribute(
								breakpoint,
								'alignSelf',
								newVal
							)
						}
					>
						<ToggleGroupControlOption value="start" label="Start" />
						<ToggleGroupControlOption
							value="center"
							label="Centered"
						/>
						<ToggleGroupControlOption value="end" label="End" />
						<ToggleGroupControlOption
							value="baseline"
							label="Baseline"
						/>
						<ToggleGroupControlOption
							value="stretch"
							label="Stretch (default)"
						/>
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
