/**
 * External dependencies
 */
import clsx from 'clsx';

/**
 * WordPress dependencies
 */
import { Icon, cog } from '@wordpress/icons';
import {
	CheckboxControl,
	RangeControl,
	Button,
	__experimentalToolsPanel as ToolsPanel,
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export function getClassName( attributes ) {
	return clsx(
		attributes.sizes && {
			[ `h-${ attributes.sizes.height }` ]:
				attributes.sizes.height !== undefined &&
				attributes.sizes.height !== 'undefined',
			[ `w-${ attributes.sizes.width }` ]:
				attributes.sizes.width !== undefined &&
				attributes.sizes.width !== 'undefined',
		}
	);
}

export function SizesPanel( { attributes, setAttribute, supportedSettings } ) {
	const resetAll = () => {
		setAttribute( 'sizes', 'height', undefined );
		setAttribute( 'sizes', 'width', undefined );
	};

	const titleStyle = { gridColumn: 'span 2' };

	return (
		<ToolsPanel label="Dimensions" resetAll={ resetAll }>
			<small className="text-muted" style={ titleStyle }>
				Configure component sizes
			</small>

			{ supportedSettings.height && (
				<ToolsPanelItem
					hasValue={ () => attributes.sizes.height !== undefined }
					label={ __( 'Block size height' ) }
					onDeselect={ () =>
						setAttribute( 'sizes', 'height', undefined )
					}
				>
					<ToggleGroupControl
						label="Block height"
						value={ attributes.sizes.height }
						onChange={ ( newVal ) =>
							setAttribute( 'sizes', 'height', newVal )
						}
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
					>
						{ [
							{
								id: 'undefined',
								name: 'none',
							},
							{
								id: '25',
								name: '25%',
							},
							{
								id: '50',
								name: '50%',
							},
							{
								id: '75',
								name: '75%',
							},
							{
								id: '100',
								name: '100%',
							},
							{
								id: 'auto',
								name: 'auto',
							},
						].map( ( item ) => {
							return (
								<ToggleGroupControlOption
									className="align-items-start"
									key={ item.id }
									value={ item.id }
									label={ item.name }
								/>
							);
						} ) }
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
			{ supportedSettings.width && (
				<ToolsPanelItem
					hasValue={ () => attributes.sizes.width !== undefined }
					label={ __( 'Block size width' ) }
					onDeselect={ () =>
						setAttribute( 'sizes', 'width', undefined )
					}
				>
					<ToggleGroupControl
						label="Block width"
						value={ attributes.sizes.width }
						onChange={ ( newVal ) =>
							setAttribute( 'sizes', 'width', newVal )
						}
						isBlock
						__nextHasNoMarginBottom
						__next40pxDefaultSize
					>
						{ [
							{
								id: 'undefined',
								name: 'none',
							},
							{
								id: '25',
								name: '25%',
							},
							{
								id: '50',
								name: '50%',
							},
							{
								id: '75',
								name: '75%',
							},
							{
								id: '100',
								name: '100%',
							},
							{
								id: 'auto',
								name: 'auto',
							},
						].map( ( item ) => {
							return (
								<ToggleGroupControlOption
									className="align-items-start"
									key={ item.id }
									value={ item.id }
									label={ item.name }
								/>
							);
						} ) }
					</ToggleGroupControl>
				</ToolsPanelItem>
			) }
		</ToolsPanel>
	);
}
